import {useState,useRef} from "react";

const ViewCalendar = (props) => {
    const {setIsViewModal,saveDate,viewData,setData} = props;

    const [textValue, setTextValue] = useState(viewData.title);
    const handleSetValue = (e) => {
        setTextValue(e.target.value);
    };

    const saveHandler = (mode) =>{
        if(textValue !== "") {
            if (window.confirm("일정을 "+(mode == 'update'?'수정':'삭제')+" 할까요?")) {
                if (localStorage.getItem("dates")) {
                    let copy = JSON.parse(localStorage.getItem("dates")|| '[]');
                    if(mode == 'update'){
                        copy.filter((findData) => {
                            if(findData.id === Number(viewData.id)){
                                findData.title = textValue
                            }
                        })

                    }else if(mode =='delete'){
                        let deleteData  = copy.filter((findData) =>{

                            return findData.id !== Number(viewData.id)
                        })
                        copy = deleteData;
                    }
                    localStorage.setItem("dates", JSON.stringify(copy))
                    setData(copy);
                    setIsViewModal(false);

                }
            }
        }else{
            alert("먼저 일정을 입력해 주세요")
        }
    }
    return (
        <div className="modalWrapper">
            <div className="modal">
                <form>
                    <div className="viewDate">
                        <label>일정 : {viewData.start} {((viewData.start != viewData.end) && "~"+ viewData.end)} </label>
                    </div>
                    <div className="group">
                            <textarea
                                rows={4}
                                onChange={(e) => handleSetValue(e)}
                                defaultValue={viewData.title}
                            >
                            </textarea><span className="highlight"></span><span className="bar"></span>
                    </div>
                    <div className="btn-box">
                        <a className="btn btn-submit" onClick={() =>{saveHandler('update')}}>수정</a>
                        <a className="btn btn-danger" onClick={() =>{saveHandler('delete')}} >삭제</a>
                        <a className="btn btn-cancel" onClick={() =>{setIsViewModal(false)}} >취소</a>
                    </div>
                </form>
            </div>
        </div>
    );
}


export default ViewCalendar;