import {useState,useRef} from "react";

const SaveCalendar = (props) => {
    const {setIsSaveModal,saveDate,setData} = props;
    const [colorValue, setColorValue] = useState("");
    const [textValue, setTextValue] = useState("");
    const handleSetValue = (e) => {
        setTextValue(e.target.value);
    };

    const handleSetRadio = (e) =>{
        console.log(e.target.value);
        if(e.target.value === 'blue'){
            setColorValue("blue")
        }else if(e.target.value === 'red'){
            setColorValue("red")
        }else if(e.target.value === 'yellow'){
            setColorValue("yellow")
        }else if(e.target.value === 'green'){
            setColorValue("green")
        }else{
            setColorValue("")
        }

    }
    const saveHandler = () =>{

        if(textValue !== "") {
            if (window.confirm("일정을 등록할까요?")) {
                if (localStorage.getItem("dates")) {
                    let copy = JSON.parse(localStorage.getItem("dates")|| '[]');
                    copy.push({title: textValue, start: saveDate, end: saveDate,id:copy.length+1,color:colorValue, textColor: (colorValue == 'yellow' ? 'black' :'white') })
                    localStorage.setItem("dates", JSON.stringify(copy))
                    setData(copy);
                    setIsSaveModal(false);
                } else {
                    localStorage.setItem("dates", JSON.stringify([{title: textValue, start: saveDate, end: saveDate,id:1,color:colorValue, textColor: (colorValue == 'yellow' ? 'black' :'white')}]))
                    setData([{title: textValue, start: saveDate, end: saveDate,id:1,color:colorValue, textColor: (colorValue == 'yellow' ? 'black' :'white')}]);
                    setIsSaveModal(false);
                }
            }
        }else{
            alert("먼저 일정을 입력해 주세요")
        }
    }
    return (
        <div className="modalWrapper">
            <div className="modal">
                <form>
                    <div className="group">
                        <label>생성일 : {saveDate} </label>
                    </div>
                    <div className="group-flex">
                        <input type="radio" name="color" value="blue"  onClick={(e) => handleSetRadio(e)}/>파랑
                        <input type="radio" name="color" value="red" onClick={(e) => handleSetRadio(e)}/>빨강
                        <input type="radio" name="color" value="yellow"  onClick={(e) => handleSetRadio(e)}/>노랑
                        <input type="radio" name="color" value="green" onClick={(e) => handleSetRadio(e)}/>초록
                        <span>(미선택시 보라색 입니다.)</span>
                    </div>
                    <div className="group">
                            <textarea
                                rows={3}
                                onChange={(e) => handleSetValue(e)}
                            >
                            </textarea><span className="highlight"></span><span className="bar"></span>
                        <label>일정을 입력하세요</label>
                    </div>
                    <div className="btn-box">
                        <a className="btn btn-link" onClick={() =>{saveHandler()}}>일정 생성</a>
                        <a className="btn btn-cancel" onClick={() =>{setIsSaveModal(false)}} >취소</a>
                    </div>
                </form>
            </div>
        </div>
    );
}


export default SaveCalendar;